.container-tabs-jobs {
  max-width: 1250px;
  margin: 0 auto;

  .react-tabs__tab-panel {
    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.08);
    background-color: #ffffff;
    border-radius: 20px 20px 20px 20px;
  }

  .react-tabs__tab-list {
    display: flex;
    overflow: auto;
    white-space: nowrap;
    flex-wrap: inherit;
    padding: 0;
    margin: 0;
    max-width: 840px;

    .react-tabs__tab {
      display: block;
      width: 100%;
      margin: 0;
      border: 1px solid rgba(0, 0, 0, 0.11);
      background-color: #0a134f;
      border-radius: 25px 25px 0 0;
      text-align: center;
      color: #ffffff;
      cursor: pointer;
      margin-right: 20px;

      .title-tab {
        font-size: 23px;
        font-weight: 500;
        margin-top: 5px;
        margin-bottom: 5px;
        padding: 5px;
      }

      img {
        margin: 0px auto 10px;
        width: 40px;
      }
    }

    .react-tabs__tab--selected {
      color: white;
      background-image: linear-gradient(-10deg, #f04e21 0%, #f8a02c 100%);
    }
  }

  .container-tab-body {
    align-self: center;
  }

  .container-tab-body {
    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.08);
    background-color: #ffffff;
    max-width: 1100px;
    margin: 0 auto;
    border-radius: 8px;

    .image-tab-body {
      width: 100%;
    }

    .option-text-body {
      text-align: center;

      border-radius: 25px;
      border: 1px solid rgba(0, 0, 0, 0.2);
      white-space: pre;

      &:hover {
        background-image: linear-gradient(-10deg, #f04e21 0%, #f8a02c 100%);
        color: white;
        border: 1px solid transparent;
        cursor: pointer;
      }
    }
  }
}
